  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  border: solid 1px white;
  color: #333333;
  font-family: "Ubuntu", sans-serif;
}

#work-description {
  width: 70%;
  padding: 16px;
}

@media only screen and (max-width: 1000px) {
